import React, { useState, useEffect, useRef, useCallback } from "react";

// 隨機生成數字
const randomNum = (min: number, max: number): number => Math.floor(Math.random() * (max - min) + min);

// 隨機生成 rgba 色碼
const randomColor = (min: number, max: number): string => {
  const r = randomNum(min, max);
  const g = randomNum(min, max);
  const b = randomNum(min, max);
  return `rgba(${r},${g},${b},1)`;
};

// 定義組件屬性的接口
interface PicAuthCodeProps {
  onCodeChange: (code: string) => void;
  reloadVerifyCode?: boolean;
  setReloadVerifyCode?: (load: boolean) => void;
}

const PicAuthCode: React.FC<PicAuthCodeProps> = (props) => {
  const [identifyCode, setIdentifyCode] = useState<string>(generateCode());
  const canvasRef = useRef<HTMLCanvasElement | null>(null);

  // 生成驗證碼
  function generateCode(): string {
    const words = "QWERTYUIPASDFGHJKLZXCVBNM123456789";
    let code = "";

    // 驗證碼為四碼
    for (let i = 0; i < 4; i++) {
      code += words[Math.floor(Math.random() * words.length)];
    }
    return code;
  }

  const { onCodeChange } = props;

  useEffect(() => {
    // 初始化時傳遞初始驗證碼給父組件
    onCodeChange(identifyCode);
  }, [identifyCode, onCodeChange]);

  const drawText = useCallback(
    (ctx: CanvasRenderingContext2D, txt: string, i: number, config: any) => {
      ctx.fillStyle = randomColor(config.colorMin, config.colorMax);
      ctx.font = `bold ${randomNum(config.fontSizeMin, config.fontSizeMax)}px sans-serif`;

      const x = (i + 1) * (config.contentWidth / (identifyCode.length + 1));
      const y = randomNum(config.fontSizeMax, config.contentHeight - 12);
      const deg = randomNum(-45, 45);

      ctx.translate(x, y);
      ctx.rotate((deg * Math.PI) / 180);
      ctx.fillText(txt, 0, 0);
      ctx.rotate((-deg * Math.PI) / 180);
      ctx.translate(-x, -y);
    },
    [identifyCode]
  );

  const drawLine = useCallback((ctx: CanvasRenderingContext2D, config: any) => {
    for (let i = 0; i < 3; i++) {
      ctx.strokeStyle = randomColor(config.lineColorMin, config.lineColorMax);
      ctx.beginPath();
      ctx.moveTo(randomNum(0, config.contentWidth), randomNum(0, config.contentHeight));
      ctx.lineTo(randomNum(0, config.contentWidth), randomNum(0, config.contentHeight));
      ctx.stroke();
    }
  }, []);

  const drawDot = useCallback((ctx: CanvasRenderingContext2D) => {
    for (let i = 0; i < 10; i++) {
      ctx.fillStyle = randomColor(0, 255);
      ctx.beginPath();
      ctx.arc(
        randomNum(0, canvasRef.current!.width),
        randomNum(0, canvasRef.current!.height),
        1,
        0,
        2 * Math.PI
      );
      ctx.fill();
    }
  }, []);

  const refresh = useCallback(() => {
    const newCode = generateCode();
    setIdentifyCode(newCode);
    props.onCodeChange(newCode); // 傳遞新的驗證碼給父組件
  }, [props]);

  const handleClick = useCallback(() => {
    refresh();
  }, [refresh]);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    const config = {
      contentWidth: 100,
      contentHeight: 40,
      backgroundColorMin: 180,
      backgroundColorMax: 240,
      fontSizeMin: 20,
      fontSizeMax: 30,
      colorMin: 50,
      colorMax: 160,
      lineColorMin: 40,
      lineColorMax: 180,
    };

    ctx.clearRect(0, 0, config.contentWidth, config.contentHeight);
    ctx.fillStyle = randomColor(config.backgroundColorMin, config.backgroundColorMax);
    ctx.fillRect(0, 0, config.contentWidth, config.contentHeight);

    identifyCode.split("").forEach((char, i) => drawText(ctx, char, i, config));
    drawLine(ctx, config);
    drawDot(ctx);
  }, [identifyCode, drawText, drawLine, drawDot]);

  useEffect(() => {
    if (props.reloadVerifyCode && props.setReloadVerifyCode) {
      refresh();
      props.setReloadVerifyCode(false);
    }
  }, [props.reloadVerifyCode])

  return (
    <canvas
      ref={canvasRef}
      width={100}
      height={40}
      onClick={handleClick}
      style={{ cursor: "pointer", borderRadius: "0 5px 5px 0px" }}
    />
  );
};

export default PicAuthCode;
