import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { Divider } from "@mui/material";
import { colors } from "../../../../styles/colors";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "../../../../store";
import { contact } from "../../../../store/userInfoSlice";
import { getFontClass } from "../../../../utils/helper";

interface RecordItem {
  date: string;
  type: string;
  question: string;
  replyTime: string;
  ans: string;
}

interface RecordsProps {
  setTabIndex: (index: number) => void;
}

const Records: React.FC<RecordsProps> = ( {setTabIndex} ) => {
  const { t } = useTranslation();
  const dispatch:AppDispatch = useDispatch();
  const contactRecords = useSelector((state: RootState) => state.userInfo.contactRecords);
  const language = useSelector((state: RootState) => state.init.language);
  const [recordsList, setRecordList] = useState<RecordItem[]>([]); 

  useEffect(() => {
    dispatch(contact({}));
  }, [dispatch]);

  useEffect(() => {
    type TypeRef = {
      '1': string;
      '2': string;
      '3': string;
      '4': string;
      '5': string;
      '6': string;
    };
  
    const typeRef: TypeRef = {
      '1': t('付款問題'),
      '2': t('帳號問題'),
      '3': t('播放問題'),
      '4': t('付款完成，系統未加值'),
      '5': t('取消月扣'),
      '6': t('其他問題')
    };
    console.log(contactRecords);
    if (typeof contactRecords === "object" && contactRecords !== null) {
      contactRecords?.contacts.forEach((item, index) => {
        const record:RecordItem[] = [{
          date: item.contact_time,
          type: typeRef[item.site_type_id as keyof TypeRef],
          question: item.content,
          replyTime: item.recontact_time ? item.recontact_time : '',
          ans: item.recontent
        }];

        if (index === 0) {
          setRecordList([]);
          setRecordList(record);
        } else {
          setRecordList((prevRecordList) => [...prevRecordList, ...record]);
        }
      });
    }
  }, [contactRecords]);
  const record: RecordItem[] = [
    {
      date: "2024-02-27 17:35:18",
      type: t("付款問題"),
      question: t("我想要退費，請問該怎麼做呢?"),
      replyTime: "2024-02-28 11:30:58",
      ans: t(
        "請告訴我們你的姓名、Email、卡號後四碼，供我們核對身分辦理取消月扣使用，謝謝"
      ),
    },
    {
      date: "2024-05-27 07:35:18",
      type: t("付款完成,系統未加值"),
      question: t("我付完款後，發現沒有成功加值，需要請你們協助處理"),
      replyTime: "2024-05-29 00:41:02",
      ans: t(
        "請告訴我們你的帳號、Email、卡號後四碼，供我們核對身分查明是否付款完成使用，謝謝"
      ),
    },
    {
      date: "2023-11-27 10:11:08",
      type: t("播放問題"),
      question: t("今天看影片時無法放大到全螢幕，不知原因為何?"),
      replyTime: "2023-11-27 14:20:42",
      ans: `親愛的會員您好<br /> <br /> 感謝您的支持~也歡迎再度蒞臨^^<br /> <br /> 請在【七天內】按下方限時連結完成取消:<br /> <a 
      href="http://ohtv.club/pay/pay.php? 
      page=pay_cancel&amp;info=q57uK5FLdLWsfinKxSZOSCIOhBqGM1YN2McU 
      target="_blank" title="http://ohtv.club/pay/pay.php? 
      page=pay_cancel&amp;info=q57uK5FLdLWsfinKxSZ0SCIOhBqGM1YN2McU 
      rel="nofollow noreferrer 
      noopener">http://ohtv.club/pay/pay.php? 
      page=pay_cancel&amp;info=q57uK5FLdLWsfinKxSZOSCIOhBqGMlYN2McU 
      <br /> <br /> ※取消成功後,電信會發送簡訊通知,若有輸入E-mail 也會收到信件通知,也可致電所屬電信客服做確認。<br /> ※取消若有 -異常,請提供【完整異常頁面】,以利查詢與確認。<br /> <br /> 歡迎來信客服共同提升觀賞體驗,收視愉快♥️`,
    },
  ];

  return (
    <div className="p-2">
      {recordsList.map((item: RecordItem, index: any) => (
        <div key={index} className={`${getFontClass(`${language}_m`)} rounded p-4 bg-background mb-4 text-sm`}>
          <div className="grid grid-cols-[70px_1fr] mb-1 texy-center">
            <span>{t("問題類型")}</span>
            <span>{item.type}</span>
          </div>
          <div className="mb-2 text-xs">{item.date}</div>
          <div className="mb-2">{item.question}</div>
          <Divider sx={{ bgcolor: colors.primary.active }} />
          <p className="my-2">{t("客服回覆")}</p>
          <div className="mb-2 text-xs">{item.replyTime}</div>
          <div className="mb-2 break-words" dangerouslySetInnerHTML={{ __html: item.ans }}/>
          <div  className="mt-3 text-center block text-secondary" onClick={() => setTabIndex(0)}>
            {t("無法為您解答？")}
          </div>
        </div>
      ))}
    </div>
  );
};
export default Records;