import React from "react";
import { Input } from "@mui/base/Input";

interface TextInputProps {
  value: string;
  onInputChange: (value: any) => void;
  placeholder?: string;
  type?: string;
  disabled?: boolean;
  multiline?: boolean;
  rows?: number;
  accept?: string;
  autocomplete?: string;
  hidden?: boolean;
  authCode?: boolean;
  rightIcon?: React.ReactNode; // 接受 rightIcon 作為一個可選的 React 元素
  className?: string;
}

const TextInput: React.FC<TextInputProps> = (props) => {
  const handleChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    if (props.type === "file") {
      props.onInputChange((event.target as HTMLInputElement).files?.[0]);
    } else {
      props.onInputChange(event.target.value);
    }
  };
  //w-full text-sm leading-5 px-3 py-2 rounded focus:shadow-lg border border-[#555555] bg-white text-black focus-visible:outline-0 min-h-10 ${props.className}
  const commonProps = {
    className:
      `w-full text-sm px-3  min-h-10 text-black rounded border-2 border-[#555555] focus-visible:outline-0 ${props.className}`,
    disabled: props.disabled,
    value: props.value,
    onChange: handleChange,
    placeholder: props.placeholder,
    autoComplete: props.autocomplete || "off",
    hidden: props.hidden,
    style: props.authCode
      ? { borderTopRightRadius: 0, borderBottomRightRadius: 0 }
      : undefined,
  };

  return (
    <div className="relative flex items-center">
      {props.multiline ? (
        <textarea {...commonProps} rows={props.rows || 1} />
      ) : (
        <input {...commonProps} type={props.type || "text"} accept={props.accept} />
      )}
      {props.rightIcon && (
        <div className="absolute right-2">
          {props.rightIcon}
        </div>
      )}
    </div>
  );
};

export default TextInput;
