import { ChangeEvent, FormEvent, useEffect, useRef, useState } from "react";
import { useTranslation } from "react-i18next";

import FormControl from "@mui/material/FormControl";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import { FilledInput, InputProps, styled } from "@mui/material";

import TextInput from "../../../../components/TextInput";
import Button from "../../../../components/Button";
import PicAuthCode from "../../../../components/PicAuthCode";

import { colors } from "../../../../styles/colors";
import { AppDispatch, RootState } from "../../../../store";
import { useDispatch, useSelector } from "react-redux";
import { contact } from "../../../../store/userInfoSlice";
import { ContactParam } from "../../../../type/userType";
import { showCustomToast } from "../../../../components/CustomToast";
import { getFontClass } from "../../../../utils/helper";
import { useSelect } from "@mui/base";

const FileInputWrapper = styled("div")({
  display: "flex",
  alignItems: "center",
  width: "100%",
});

const HiddenFileInput = styled("input")({});

const CustomerServiceForm: React.FC = () => {
  const dispatch: AppDispatch = useDispatch();
  const { t } = useTranslation();
  const fileInputRef = useRef<HTMLInputElement>(null);
  const language = useSelector((state: RootState) => state.init.language);
  const email = useSelector((state: RootState) => state.userInfo.userInfoData?.data?.email ?? "");
  const userName = useSelector((state: RootState) => state.userInfo.userInfoData?.data?.username ?? "");

  const [reloadVerifyCode, setReloadVerifyCode] = useState<boolean>(false);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [formData, setFormData] = useState<ContactParam>({
    type: "",
    content: "",
    nickname: userName,
    phone: "",
    email: email,
    vcode: "",
    img: null,
  });
  const [receivedCode, setReceivedCode] = useState<string>("");

  const handleFileChange = (event: ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files.length > 0) {
      handleChange("img", event.target.files[0]);
      //setSelectedFile(event.target.files[0]);
    }
  };

  const handleChange = (
    name: keyof ContactParam,
    value: string | number | File
  ) => {
    setFormData((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  const handleSubmit = async () => {
    if (formData.vcode?.toLowerCase() !== receivedCode.toLowerCase()) {
      alert(t("驗證碼錯誤，請重新輸入。"));
      return;
    }
    if (
      formData.content === "" ||
      formData.email === "" ||
      formData.nickname === "" ||
      formData.type === ""
    ) {
      alert(t("必填欄位請填寫完整！"));
      return;
    }

    const initData = {
      type: "",
      content: "",
      nickname: userName,
      phone: "",
      email: email,
      vcode: "",
      img: null,
    };
    
    setFormData(initData);
    setReloadVerifyCode(true);

    dispatch(contact(formData))
      .then((result) => {
        const msg =
          result.payload.msg === "complete"
            ? t("發送成功！")
            : t("發送失敗，請重新發送！");
        if (result.meta.requestStatus === "fulfilled") {
          showCustomToast(msg, "error");
        } else {
          showCustomToast(msg, "error");
          console.error("Error:", result.payload.msg); // 錯誤訊息
        }
        dispatch(contact({}));
      })
      .catch((err) => {
        console.error("Unexpected Error:", err);
      });
    
  };

  const handleCodeChange = (code: string) => {
    setReceivedCode(code);
  };

  return (
    <div className="p-4 rounded text-sm">
      <p className={`${getFontClass(`${language}_m`)} mb-4`}>
        {t("回覆時間(例假日除外)")}︰ <br />
        {t("週一至週五 09：00 ~ 18：00 (UTC+8)")}
      </p>

      <form className={`${getFontClass(`${language}_m`)} space-y-4`}>
        <div>
          <label className="block mb-2">
            <span className="text-secondary">*</span>
            {t("問題類型")}
          </label>
          <FormControl fullWidth>
            <Select
              value={formData.type}
              onChange={(e: SelectChangeEvent<number | string>) =>
                handleChange("type", e.target.value)
              }
              displayEmpty
              className="bg-[#ddd] focus-visible:outline-0"
              sx={{
                color: colors.primary,
                "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                  borderColor: colors.secondary,
                  borderWidth: "1px",
                },
              }}
              SelectDisplayProps={{
                style: { paddingTop: 10, paddingBottom: 10 },
              }}
            >
              <MenuItem value="" disabled>
                {t("請選擇問題")}
              </MenuItem>
              <MenuItem value={1}>{t("付款問題")}</MenuItem>
              <MenuItem value={2}>{t("帳號問題")}</MenuItem>
              <MenuItem value={3}>{t("播放問題")}</MenuItem>
              <MenuItem value={4}>{t("付款完成，系統未加值")}</MenuItem>
              <MenuItem value={5}>{t("取消月扣")}</MenuItem>
              <MenuItem value={99}>{t("其他問題")}</MenuItem>
            </Select>
          </FormControl>
        </div>
        <div>
          <TextInput
            className="bg-[#ddd] w-full rounded placeholder-stone-700 border-none"
            placeholder={t("請詳述問題")}
            value={formData.content ?? ""}
            onInputChange={(value: string) => handleChange("content", value)}
            rows={6}
          />
        </div>
        <div>
          <label className="block mb-2">{t("圖片上傳")}</label>
          <FileInputWrapper>
            <HiddenFileInput
              ref={fileInputRef}
              type="file"
              accept="image/*"
              onChange={handleFileChange}
            />
          </FileInputWrapper>
        </div>
        <div className={`${getFontClass(`${language}_m`)} w-full`}>
          <label className="block mb-2">
            <span className="text-secondary">*</span>
            {t("姓名")}
          </label>
          <TextInput
            className="bg-[#ddd] w-full rounded placeholder-stone-700 border-none"
            placeholder={t("姓名")}
            value={formData.nickname ?? userName}
            onInputChange={(value: string) => handleChange("nickname", value)}
          />
        </div>
        <div>
          <label className="block mb-2">{t("連絡電話")}</label>
          <TextInput
            className="bg-[#ddd] w-full rounded placeholder-stone-700 border-none"
            placeholder={t("連絡電話")}
            value={formData.phone ?? ""}
            onInputChange={(value: string) => handleChange("phone", value)}
          />
        </div>
        <div>
          <label className="block mb-2">
            <span className="text-secondary">*</span>Email
          </label>
          <TextInput
            className="bg-[#ddd] w-full rounded placeholder-stone-700 border-none"
            placeholder="Email"
            value={formData.email ?? email}
            onInputChange={(value: string) => handleChange("email", value)}
          />
        </div>
        <div>
          <label className="block mb-2">
            <span className="text-secondary">*</span>
            {t("輸入驗證碼")}
          </label>
          <div className="flex flex-row">
            <TextInput
              className="bg-[#ddd] w-full rounded placeholder-stone-700 border-none"
              authCode={true}
              placeholder={t("輸入驗證碼")}
              value={formData.vcode ?? ""}
              onInputChange={(value: string) => handleChange("vcode", value)}
            />
            <PicAuthCode onCodeChange={handleCodeChange} reloadVerifyCode={reloadVerifyCode} setReloadVerifyCode={setReloadVerifyCode}/>
          </div>
        </div>
        <div>
          <Button className="button-full active h-10" onClick={handleSubmit}>
            {t("發送")}
          </Button>
        </div>
      </form>
    </div>
  );
};
export default CustomerServiceForm;
